.header {
  width: 100%;
  background-color: #343F3E;
  padding: 0; }

.navButtons {
  margin: 0 auto;
  width: 75%; }

.navListWrapper {
  padding: 0 0 0 0;
  margin: 0;
  list-style-type: none; }

.navList {
  display: inline;
  text-align: center; }

.item {
  display: inline-block;
  width: 33%;
  padding: 0.75rem 0;
  margin: 0;
  text-decoration: none;
  color: #fff;
  border: none;
  background-color: #343F3E;
  opacity: 0.5; }
  .item:focus {
    outline: none;
    opacity: 1; }

.item:hover {
  opacity: 1; }

.home:hover ~ hr {
  margin-left: 0%;
  background: #8bbbb2; }

.gallery:hover ~ hr {
  margin-left: 33%;
  background: #8bbbb2; }

.cv:hover ~ hr {
  margin-left: 66%;
  background: #8bbbb2; }

.homeSelect ~ hr {
  margin-left: 0%; }

.gallerySelect ~ hr {
  margin-left: 33%; }

.cvSelect ~ hr {
  margin-left: 66%; }

.slidingHR {
  height: 0.25rem;
  width: 33%;
  margin: 0;
  background: #538D81;
  border: none;
  transition: 0.3s ease-in-out; }

.footer {
  padding-left: 10%;
  padding-right: 10%;
  font-size: 50px;
  display: flex;
  justify-content: flex-end;
  flex-shrink: 0; }

.footerIcon {
  color: #d6d6d6; }

.footerIcon:hover {
  cursor: pointer;
  color: #fff; }

.homePage {
  background-color: #343F3E;
  display: flex;
  flex-direction: column;
  flex: 1 1; }

.homeIntro {
  padding-top: 5%;
  padding-left: 10%;
  font-size: 5em;
  color: #fff; }

.homeText {
  padding-left: 10%;
  font-size: 3em;
  color: #fff; }

.homeEnding {
  padding-left: 10%;
  font-size: 2em;
  color: #fff; }

.galleryPage {
  padding: 0 10% 10%;
  display: flex;
  flex-direction: column;
  flex-grow: 1; }

.galleryHeader {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff; }

.galleryGrid {
  display: grid;
  grid-column-gap: 5%;
  grid-row-gap: 5%;
  grid-template-columns: 25% 25% 25%;
  grid-template-rows: auto;
  justify-content: space-between; }

.galleryItem {
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.75);
  justify-self: center;
  display: flex;
  flex-direction: column;
  min-height: 250px; }

.galleryItemContent {
  position: relative;
  width: 100%;
  display: flex;
  justify-self: center;
  align-items: center;
  flex: 1 1; }

.galleryItemHeader {
  display: flex;
  justify-content: center;
  color: #fff;
  background-color: #538D81; }

.galleryItemOverlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  color: #fff;
  background-color: #538D81; }

.galleryItemContent:hover .galleryItemOverlay {
  opacity: 1;
  cursor: pointer; }

.overlayText {
  text-align: center;
  padding-top: 35%; }

.hidden {
  display: none; }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4); }

.modalContent {
  position: fixed;
  top: 15%;
  left: 20%;
  width: 60%;
  height: 70%;
  background-color: #538D81;
  display: grid;
  grid-template-columns: 70% 30%;
  grid-template-rows: 7% 100%;
  grid-column-gap: 2px;
  -webkit-column-gap: 2px;
          column-gap: 2px;
  grid-row-gap: 2px;
  row-gap: 2px;
  grid-template-areas: "modalHeader modalHeader" "contentImage contentDescription"; }

.modalHeader {
  grid-area: modalHeader;
  display: flex;
  justify-content: space-between;
  color: #fff;
  background-color: #343F3E;
  width: 100%; }

.modalTitle {
  font-size: xx-large;
  padding-left: 10px;
  padding-top: 5px; }

.modalClose {
  font-size: x-large;
  padding-right: 10px;
  padding-top: 10px; }

.modalClose:hover {
  cursor: pointer; }

.contentImage {
  grid-area: contentImage;
  align-self: center;
  justify-self: center;
  background-color: #343F3E;
  width: 100%;
  height: 100%; }

.contentDescription {
  grid-area: contentDescription;
  color: #fff;
  background-color: #343F3E;
  width: 100%;
  height: 100%;
  white-space: pre-wrap;
  padding-left: 2px; }

.projectLink {
  color: #fff; }

a:link:active, a:visited:active {
  color: #fff; }

* {
  box-sizing: border-box; }

/* Position the image container (needed to position the left and right arrows) */
.slideshowContainer {
  display: flex;
  flex-direction: column;
  height: 100%; }

/* Hide the images by default */
.mySlides {
  display: none;
  justify-content: center;
  object-fit: contain; }

.largeImage {
  object-fit: contain; }

.slideContainer {
  display: flex;
  justify-content: center;
  align-content: center;
  height: 80%; }

/* Add a pointer when hovering over the thumbnail images */
.cursor {
  cursor: pointer; }

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8); }

.row:after {
  content: "";
  display: table;
  clear: both; }

.row {
  display: flex;
  justify-content: center;
  height: 20%; }

/* Six columns side by side */
/*TODO: update widths dynamically for number of images: flex? */
.column {
  max-width: 20%;
  display: flex;
  align-content: space-evenly; }

/* Add a transparency effect for thumnbail images */
.demo {
  opacity: 0.5; }

.active,
.demo:hover {
  opacity: 1; }

.resumeText {
  margin-top: 5%;
  text-align: center;
  color: #fff; }

.resumeContainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  color: #fff; }

.downloadContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly; }

.iconContainer {
  margin-top: 10%; }

.resumeIcon {
  color: #d6d6d6;
  font-size: 150px; }

.resumeIcon:hover {
  color: #fff;
  transition: 0.3s ease-in-out;
  cursor: pointer; }

.downloadText {
  font-size: 30px;
  text-align: center; }

html,
body {
  height: 100%;
  background-color:#343F3E;
  /*color: #1c2541;*/
  margin: 0;
  font-family: 'Lato', 'Helvetica', 'sans-serif';
}

.root {
  height: 100%;
}

.pageContainer {
  padding-left: 10%;
  padding-right: 10%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 100%;
}

