.galleryPage {
  padding: 0 10% 10%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.galleryHeader {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
}

.galleryGrid {
  display: grid;
  grid-column-gap: 5%;
  grid-row-gap: 5%;
  grid-template-columns: 25% 25% 25%;
  grid-template-rows: auto;
  justify-content: space-between;
}

.galleryItem {
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
  justify-self: center;
  display: flex;
  flex-direction: column;
  min-height: 250px;
}

.galleryItemContent {
  position: relative;
  width: 100%;
  display: flex;
  justify-self: center;
  align-items: center;
  flex: 1;
}

.galleryItemHeader {
  display: flex;
  justify-content:center;
  color: #fff;
  background-color: #538D81;
  
}

.galleryItemOverlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  color: #fff;
  background-color: #538D81;
}

.galleryItemContent:hover .galleryItemOverlay {
  opacity: 1;
  cursor: pointer;
}

.overlayText {
  text-align: center;
  padding-top: 35%;
}